import React, { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const CaseAnalysis = () => {
  const [formData, setFormData] = useState({
    // 1. 사건 기본 정보
    schoolLevel: 'middle', // 초기값: 중학교
    title: '학교 내 집단 따돌림 사건',
    incidentDate: '2024-03-15',
    incidentTime: '13:30',
    location: 'school',
    region: 'seoul', // 발생지역 추가
    
    // 2. 가해/피해 정보
    perpetratorCount: '3',
    victimCount: '1',
    perpetratorGrade: '2',
    perpetratorGender: 'male',
    victimGrade: '2',
    victimGender: 'male',
    
    // 3. 사건 상세 내용
    violenceTypes: {
      verbal: true,
      physical: false,
      cyber: true,
      bullying: true,
      other: false
    },
    description: '피해 학생이 2학년 반 학생들로부터 지속적인 언어폭력과 따돌림을 당했으며, 온라인상에서도 비방글을 올리는 등 사이버폭력이 가해졌습니다.',
    remorseStatus: 'no',
    
    // 4. 학교 조치 정보
    plannedAction: '7',
    additionalNotes: '가해 학생들의 반성이 없으며, 피해 학생의 정신적 고통이 심각한 상태입니다.'
  });

  const generateAnalysisResult = (formData) => {
    const violenceTypes = Object.entries(formData.violenceTypes)
      .filter(([_, value]) => value)
      .map(([key]) => key);

    // 지역별 학교 목록
    const regionSchools = {
      seoul: ['서울시 A중학교', '서울시 B중학교', '서울시 C중학교'],
      gyeonggi: ['경기도 A중학교', '경기도 B중학교', '경기도 C중학교'],
      incheon: ['인천시 A중학교', '인천시 B중학교', '인천시 C중학교'],
      busan: ['부산시 A중학교', '부산시 B중학교', '부산시 C중학교'],
      daegu: ['대구시 A중학교', '대구시 B중학교', '대구시 C중학교'],
      gwangju: ['광주시 A중학교', '광주시 B중학교', '광주시 C중학교'],
      daejeon: ['대전시 A중학교', '대전시 B중학교', '대전시 C중학교'],
      ulsan: ['울산시 A중학교', '울산시 B중학교', '울산시 C중학교'],
      sejong: ['세종시 A중학교', '세종시 B중학교', '세종시 C중학교'],
      jeju: ['제주시 A중학교', '제주시 B중학교', '제주시 C중학교']
    };

    // 전국 유사 사례 생성
    const nationalCases = violenceTypes.map((type, index) => {
      const allSchools = Object.values(regionSchools).flat();
      const types = {
        verbal: '언어폭력',
        physical: '신체폭력',
        cyber: '사이버폭력',
        bullying: '따돌림',
        other: '기타'
      };
      
      return {
        id: `national-${index + 1}`,
        date: `2024-${String(Math.floor(Math.random() * 3) + 1).padStart(2, '0')}-${String(Math.floor(Math.random() * 28) + 1).padStart(2, '0')}`,
        school: allSchools[Math.floor(Math.random() * allSchools.length)],
        type: types[type],
        similarity: Math.floor(Math.random() * 20) + 70,
        decision: `제${Math.floor(Math.random() * 3) + 6}호 조치`
      };
    });

    // 지역별 유사 사례 생성
    const regionalCases = violenceTypes.map((type, index) => {
      const types = {
        verbal: '언어폭력',
        physical: '신체폭력',
        cyber: '사이버폭력',
        bullying: '따돌림',
        other: '기타'
      };
      
      return {
        id: `regional-${index + 1}`,
        date: `2024-${String(Math.floor(Math.random() * 3) + 1).padStart(2, '0')}-${String(Math.floor(Math.random() * 28) + 1).padStart(2, '0')}`,
        school: regionSchools[formData.region][Math.floor(Math.random() * 3)],
        type: types[type],
        similarity: Math.floor(Math.random() * 20) + 80, // 지역 내 유사도가 더 높게 설정
        decision: `제${Math.floor(Math.random() * 3) + 6}호 조치`
      };
    });

    return {
      nationalCases,
      regionalCases,
      decisionDistribution: {
        national: {
          labels: ['제1-3호', '제4-6호', '제7-9호'],
          datasets: [{
            label: '전국 유사 사례 조치 수준 분포',
            data: calculateDecisionDistribution(violenceTypes),
            backgroundColor: 'rgba(75, 192, 192, 0.5)',
            borderColor: 'rgb(75, 192, 192)',
            borderWidth: 1
          }]
        },
        regional: {
          labels: ['제1-3호', '제4-6호', '제7-9호'],
          datasets: [{
            label: '지역별 유사 사례 조치 수준 분포',
            data: calculateDecisionDistribution(violenceTypes, true),
            backgroundColor: 'rgba(54, 162, 235, 0.5)',
            borderColor: 'rgb(54, 162, 235)',
            borderWidth: 1
          }]
        }
      },
      recommendedDecision: determineRecommendedDecision(violenceTypes, formData),
      confidence: calculateConfidence(violenceTypes, formData)
    };
  };

  const calculateDecisionDistribution = (violenceTypes, regional = false) => {
    if (violenceTypes.includes('physical')) {
      return [10, 30, 60]; // 신체폭력의 경우 중상위 조치 비중이 높음
    } else if (violenceTypes.includes('cyber') && violenceTypes.includes('bullying')) {
      return [15, 45, 40]; // 사이버폭력+따돌림의 경우 중간 조치 비중이 높음
    } else if (violenceTypes.includes('verbal') && violenceTypes.includes('bullying')) {
      return [20, 50, 30]; // 언어폭력+따돌림의 경우 중간 조치 비중이 높음
    }
    return [30, 40, 30]; // 기본 분포
  };

  const determineRecommendedDecision = (violenceTypes, formData) => {
    let baseDecision = 5; // 기본 조치 수준

    // 폭력 유형에 따른 조정
    if (violenceTypes.includes('physical')) baseDecision += 2;
    if (violenceTypes.includes('cyber')) baseDecision += 1;
    if (violenceTypes.includes('bullying')) baseDecision += 1;
    if (violenceTypes.includes('verbal')) baseDecision += 0.5;

    // 반성 여부에 따른 조정
    if (formData.remorseStatus === 'no') baseDecision += 1;
    if (formData.remorseStatus === 'yes') baseDecision -= 1;

    // 최종 조치 수준 결정 (1-9 사이)
    const finalDecision = Math.min(9, Math.max(1, Math.round(baseDecision)));
    return `제${finalDecision}호 조치`;
  };

  const calculateConfidence = (violenceTypes, formData) => {
    let confidence = 70; // 기본 신뢰도

    // 폭력 유형이 많을수록 신뢰도 증가
    confidence += violenceTypes.length * 5;

    // 반성 여부가 명확할수록 신뢰도 증가
    if (formData.remorseStatus !== 'unknown') confidence += 5;

    // 최대 95%로 제한
    return Math.min(95, confidence);
  };

  const [analysisResult, setAnalysisResult] = useState(generateAnalysisResult(formData));

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    if (type === 'checkbox') {
      setFormData(prev => ({
        ...prev,
        violenceTypes: {
          ...prev.violenceTypes,
          [name]: checked
        }
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }));
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setAnalysisResult(generateAnalysisResult(formData));
  };

  const handleExpertConsultation = () => {
    // 전문가 상담 요청 처리
    alert('전문가 상담 요청이 접수되었습니다. 담당자가 곧 연락드리겠습니다.');
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-blue-50 to-white">
      <div className="container mx-auto px-4 py-8">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-blue-800 mb-4">AI학폭상담소</h1>
          <p className="text-lg text-gray-600">학교폭력 사안처리 공정성 확보 시스템</p>
        </div>
        
        <div className="max-w-[1600px] mx-auto space-y-8">
          {/* 사건 정보 입력 섹션 */}
          <div className="bg-white p-8 rounded-xl shadow-lg border border-blue-100">
            <h2 className="text-2xl font-semibold mb-8 text-blue-800 flex items-center">
              <svg className="w-6 h-6 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
              </svg>
              사건 정보 입력
            </h2>
            <form onSubmit={handleSubmit} className="space-y-8">
              {/* 1. 사건 기본 정보 */}
              <div className="space-y-6">
                <h3 className="text-xl font-medium text-gray-700">1. 사건 기본 정보</h3>
                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">학교급</label>
                    <select
                      name="schoolLevel"
                      value={formData.schoolLevel}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    >
                      <option value="elementary">초등학교</option>
                      <option value="middle">중학교</option>
                      <option value="high">고등학교</option>
                    </select>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">발생지역</label>
                    <select
                      name="region"
                      value={formData.region}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    >
                      <option value="seoul">서울특별시</option>
                      <option value="gyeonggi">경기도</option>
                      <option value="incheon">인천광역시</option>
                      <option value="busan">부산광역시</option>
                      <option value="daegu">대구광역시</option>
                      <option value="gwangju">광주광역시</option>
                      <option value="daejeon">대전광역시</option>
                      <option value="ulsan">울산광역시</option>
                      <option value="sejong">세종특별자치시</option>
                      <option value="jeju">제주특별자치도</option>
                    </select>
                  </div>
                </div>
                
                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">사건 제목</label>
                    <input
                      type="text"
                      name="title"
                      value={formData.title}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    />
                  </div>
                </div>
                
                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">발생 일자</label>
                    <input
                      type="date"
                      name="incidentDate"
                      value={formData.incidentDate}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">발생 시간</label>
                    <input
                      type="time"
                      name="incidentTime"
                      value={formData.incidentTime}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    />
                  </div>
                </div>
              </div>

              {/* 2. 가해/피해 정보 */}
              <div className="space-y-6">
                <h3 className="text-xl font-medium text-gray-700">2. 가해/피해 정보</h3>
                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">가해 학생 수</label>
                    <input
                      type="number"
                      name="perpetratorCount"
                      value={formData.perpetratorCount}
                      onChange={handleChange}
                      min="1"
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">피해 학생 수</label>
                    <input
                      type="number"
                      name="victimCount"
                      value={formData.victimCount}
                      onChange={handleChange}
                      min="1"
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">가해자 학년</label>
                    <select
                      name="perpetratorGrade"
                      value={formData.perpetratorGrade}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    >
                      <option value="">선택하세요</option>
                      <option value="1">1학년</option>
                      <option value="2">2학년</option>
                      <option value="3">3학년</option>
                      <option value="4">4학년</option>
                      <option value="5">5학년</option>
                      <option value="6">6학년</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">가해자 성별</label>
                    <select
                      name="perpetratorGender"
                      value={formData.perpetratorGender}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    >
                      <option value="">선택하세요</option>
                      <option value="male">남</option>
                      <option value="female">여</option>
                    </select>
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">피해자 학년</label>
                    <select
                      name="victimGrade"
                      value={formData.victimGrade}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    >
                      <option value="">선택하세요</option>
                      <option value="1">1학년</option>
                      <option value="2">2학년</option>
                      <option value="3">3학년</option>
                      <option value="4">4학년</option>
                      <option value="5">5학년</option>
                      <option value="6">6학년</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">피해자 성별</label>
                    <select
                      name="victimGender"
                      value={formData.victimGender}
                      onChange={handleChange}
                      className="w-full p-3 border rounded-md text-lg"
                      required
                    >
                      <option value="">선택하세요</option>
                      <option value="male">남</option>
                      <option value="female">여</option>
                    </select>
                  </div>
                </div>
              </div>

              {/* 3. 사건 상세 내용 */}
              <div className="space-y-6">
                <h3 className="text-xl font-medium text-gray-700">3. 사건 상세 내용</h3>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">폭력 유형</label>
                  <div className="space-y-2">
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        name="verbal"
                        checked={formData.violenceTypes.verbal}
                        onChange={handleChange}
                        className="mr-2"
                      />
                      언어폭력
                    </label>
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        name="physical"
                        checked={formData.violenceTypes.physical}
                        onChange={handleChange}
                        className="mr-2"
                      />
                      신체폭력
                    </label>
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        name="cyber"
                        checked={formData.violenceTypes.cyber}
                        onChange={handleChange}
                        className="mr-2"
                      />
                      사이버폭력
                    </label>
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        name="bullying"
                        checked={formData.violenceTypes.bullying}
                        onChange={handleChange}
                        className="mr-2"
                      />
                      따돌림
                    </label>
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        name="other"
                        checked={formData.violenceTypes.other}
                        onChange={handleChange}
                        className="mr-2"
                      />
                      기타
                    </label>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">사건 개요</label>
                  <textarea
                    name="description"
                    value={formData.description}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-md h-32 text-lg"
                    placeholder="피해자 진술 요약 등을 입력하세요"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">가해자 반성 여부</label>
                  <select
                    name="remorseStatus"
                    value={formData.remorseStatus}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-md text-lg"
                    required
                  >
                    <option value="">선택하세요</option>
                    <option value="yes">반성함</option>
                    <option value="no">반성 안 함</option>
                    <option value="unknown">불분명</option>
                  </select>
                </div>
              </div>

              {/* 4. 학교 조치 정보 */}
              <div className="space-y-6">
                <h3 className="text-xl font-medium text-gray-700">4. 학교 조치 정보</h3>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">현재 예정된 조치안</label>
                  <select
                    name="plannedAction"
                    value={formData.plannedAction}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-md text-lg"
                  >
                    <option value="">선택하세요</option>
                    <option value="1">제1호 조치</option>
                    <option value="2">제2호 조치</option>
                    <option value="3">제3호 조치</option>
                    <option value="4">제4호 조치</option>
                    <option value="5">제5호 조치</option>
                    <option value="6">제6호 조치</option>
                    <option value="7">제7호 조치</option>
                    <option value="8">제8호 조치</option>
                    <option value="9">제9호 조치</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">의견/특이사항</label>
                  <textarea
                    name="additionalNotes"
                    value={formData.additionalNotes}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-md h-32 text-lg"
                    placeholder="추가적인 의견이나 특이사항을 입력하세요"
                  />
                </div>
              </div>

              <div className="flex space-x-6">
                <button
                  type="submit"
                  className="flex-1 bg-blue-600 text-white py-4 px-6 rounded-lg hover:bg-blue-700 text-lg font-medium transition-colors flex items-center justify-center"
                >
                  <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9.663 17h4.673M12 3v1m6.364 1.636l-.707.707M21 12h-1M4 12H3m3.343-5.657l-.707-.707m2.828 9.9a5 5 0 117.072 0l-.548.547A3.374 3.374 0 0014 18.469V19a2 2 0 11-4 0v-.531c0-.895-.356-1.754-.988-2.386l-.548-.547z" />
                  </svg>
                  AI 분석 요청하기
                </button>
                <button
                  type="button"
                  onClick={() => setFormData({
                    schoolLevel: 'middle',
                    title: '학교 내 집단 따돌림 사건',
                    incidentDate: '2024-03-15',
                    incidentTime: '13:30',
                    location: 'school',
                    region: 'seoul',
                    perpetratorCount: '3',
                    victimCount: '1',
                    perpetratorGrade: '2',
                    perpetratorGender: 'male',
                    victimGrade: '2',
                    victimGender: 'male',
                    violenceTypes: {
                      verbal: true,
                      physical: false,
                      cyber: true,
                      bullying: true,
                      other: false
                    },
                    description: '피해 학생이 2학년 반 학생들로부터 지속적인 언어폭력과 따돌림을 당했으며, 온라인상에서도 비방글을 올리는 등 사이버폭력이 가해졌습니다.',
                    remorseStatus: 'no',
                    plannedAction: '7',
                    additionalNotes: '가해 학생들의 반성이 없으며, 피해 학생의 정신적 고통이 심각한 상태입니다.'
                  })}
                  className="flex-1 bg-gray-100 text-gray-700 py-4 px-6 rounded-lg hover:bg-gray-200 text-lg font-medium transition-colors flex items-center justify-center"
                >
                  <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                  </svg>
                  초기화
                </button>
              </div>
            </form>
          </div>

          {/* 분석 결과 섹션 */}
          <div className="bg-white p-8 rounded-xl shadow-lg border border-blue-100">
            <h2 className="text-2xl font-semibold mb-8 text-blue-800 flex items-center">
              <svg className="w-6 h-6 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
              분석 결과
            </h2>
            
            {/* 전국 유사 사례 */}
            <div className="mb-12">
              <h3 className="text-xl font-medium mb-6 text-gray-700 flex items-center">
                <svg className="w-5 h-5 mr-2 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M3.055 11H5a2 2 0 012 2v1a2 2 0 002 2 2 2 0 012 2v2.945M8 3.935V5.5A2.5 2.5 0 0010.5 8h.5a2 2 0 012 2 2 2 0 104 0 2 2 0 012-2h1.064M15 20.488V18a2 2 0 012-2h3.064M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                전국 유사 사례
              </h3>
              <div className="grid grid-cols-3 gap-6">
                {analysisResult.nationalCases.map(case_ => (
                  <div key={case_.id} className="bg-white border border-blue-100 rounded-lg p-6 hover:shadow-md transition-shadow">
                    <div className="flex justify-between items-center mb-3">
                      <span className="font-medium text-lg text-blue-800">{case_.school}</span>
                      <span className="text-sm text-gray-500">{case_.date}</span>
                    </div>
                    <div className="flex justify-between items-center mb-3">
                      <span className="text-gray-600">{case_.type}</span>
                      <span className="font-medium text-blue-600">유사도: {case_.similarity}%</span>
                    </div>
                    <div className="bg-blue-50 p-3 rounded-md">
                      <span className="text-gray-700">조치: {case_.decision}</span>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* 지역별 유사 사례 */}
            <div className="mb-12">
              <h3 className="text-xl font-medium mb-6 text-gray-700 flex items-center">
                <svg className="w-5 h-5 mr-2 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
                지역별 유사 사례
              </h3>
              <div className="grid grid-cols-3 gap-6">
                {analysisResult.regionalCases.map(case_ => (
                  <div key={case_.id} className="bg-white border border-blue-100 rounded-lg p-6 hover:shadow-md transition-shadow">
                    <div className="flex justify-between items-center mb-3">
                      <span className="font-medium text-lg text-blue-800">{case_.school}</span>
                      <span className="text-sm text-gray-500">{case_.date}</span>
                    </div>
                    <div className="flex justify-between items-center mb-3">
                      <span className="text-gray-600">{case_.type}</span>
                      <span className="font-medium text-blue-600">유사도: {case_.similarity}%</span>
                    </div>
                    <div className="bg-blue-50 p-3 rounded-md">
                      <span className="text-gray-700">조치: {case_.decision}</span>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            
            {/* 전국/지역별 조치 수준 분포 */}
            <div className="mb-12">
              <h3 className="text-xl font-medium mb-6 text-gray-700 flex items-center">
                <svg className="w-5 h-5 mr-2 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                </svg>
                조치 수준 분포
              </h3>
              <div className="grid grid-cols-2 gap-8">
                <div className="bg-white p-6 rounded-lg border border-blue-100">
                  <h4 className="text-lg font-medium mb-4 text-gray-700">전국 분포</h4>
                  <div className="h-[400px]">
                    <Bar data={analysisResult.decisionDistribution.national} options={{
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          position: 'top',
                          labels: {
                            font: {
                              size: 16
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            font: {
                              size: 14
                            }
                          }
                        },
                        x: {
                          ticks: {
                            font: {
                              size: 14
                            }
                          }
                        }
                      }
                    }} />
                  </div>
                </div>
                <div className="bg-white p-6 rounded-lg border border-blue-100">
                  <h4 className="text-lg font-medium mb-4 text-gray-700">지역별 분포</h4>
                  <div className="h-[400px]">
                    <Bar data={analysisResult.decisionDistribution.regional} options={{
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          position: 'top',
                          labels: {
                            font: {
                              size: 16
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            font: {
                              size: 14
                            }
                          }
                        },
                        x: {
                          ticks: {
                            font: {
                              size: 14
                            }
                          }
                        }
                      }
                    }} />
                  </div>
                </div>
              </div>
            </div>
            
            <div className="bg-blue-50 p-8 rounded-lg mb-10 border border-blue-200">
              <h3 className="text-xl font-medium mb-4 text-blue-800">권장 조치</h3>
              <p className="text-3xl font-bold text-blue-600 mb-3">{analysisResult.recommendedDecision}</p>
              <p className="text-lg text-gray-600">신뢰도: {analysisResult.confidence}%</p>
            </div>

            <button
              onClick={handleExpertConsultation}
              className="w-full bg-green-600 text-white py-5 px-6 rounded-lg hover:bg-green-700 transition-colors text-xl font-medium flex items-center justify-center"
            >
              <svg className="w-6 h-6 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
              </svg>
              전문가 상담 요청하기
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CaseAnalysis; 